aside.sidebar {
    padding: 4rem;
}

.sidebar img {
    max-width: 50%;
    height: auto;
    border-radius: 100%;
}

.sidebar h1 {
    text-align: start;
    font-weight: 600;
    letter-spacing: 3px;
}

.sidebar .sidebar-info {
    margin-bottom: 2rem;
}

.sidebar .sidebar-about {
    margin-top: 2rem;
    margin-bottom: 2rem;
}

.sidebar .sidebar-social {
    margin-top: 2rem;
}

.sidebar .social-links {
    display: flex;
    padding: 0;  
}

.sidebar .social-links li {
    display: block;
    padding: 0 0.5rem;
}

.sidebar .sidebar-about h2, .sidebar .sidebar-social h2 {
    font-size: 18px;
    letter-spacing: 3px;
}

.sidebar .sidebar-about p {
    font-size: 14px;
    line-height: 25px;
    color: #ffffff;
}

.social-sidebar-color {
    color: #202a45;
}

.social-sidebar-color:hover {
    opacity: 0.6;
}

@media (max-width: 991.98px) {
    aside.sidebar {
        padding: 1rem;
    }
}